<template>
	<div class="login-page">
		<login @login="login"></login>
	</div>
</template>

<script>
	import login from '@/components/net/login'
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				
			}
		},
		methods:{
			login(data){
				apiMapper.manageLogin({
					username:data.username,
					password:this.$md5(data.pwd)
				}).then(res=>{
					console.log(res);
					if(res.data.code !=0){
						this.$message.error(res.data.msg)	
					}else{
						sessionStorage.setItem('userId',res.data.data.user_id);
						this.$router.push({
							path:'/manageHome'
						})
					}
				})
			},
		},
		components:{
			login
		}
	}
</script>

<style scoped>
	.login-page{
		width: 350px;
	    height: 350px;
	    background: white;
	    padding: 25px;
	    position: relative;
	    top: 20vh;
	    margin: 0 auto;
	    opacity: .9;
	}
</style>


