<template>
	<div class="detail-page">
		<div class="name">
			游戏名:{{game.game_name}}
		</div>
		<div class="abstract">
			游戏描述:{{game.abstract}}
		</div>
		<div class="time">
			创建时间:{{game.create_time}}
		</div>
		<div class="download_urls">
			下载链接：
			<a :href="x" v-for="(x,index) in game.download_urls">
				下载地址{{index+1}}
			</a>
		</div>
		<div class="urls">
			友情链接：
			<a :href="x" v-for="(x,index) in game.urls">
				地址{{index+1}}
			</a>
		</div>
		<div class="detaile">
			游戏详情:{{game.detaile}}
		</div>
		<div class="hot">
			目前热度：{{game.hot}}
		</div>
		<div class="photoes" v-if="imgLength">
			游戏原图:
			<div class="">
				<img :src="x" v-for="x in game.photoes"/>
			</div>
		</div>
		<div class="video" v-if="game.video">
			<video width="800" height="400">
				<source :src="game.video" type="video/mp4"></source>
				<object width="" height="" type="application/x-shockwave-flash" data="myvideo.swf">
					<param name="movie" value="myvideo.swf" />
					<param name="flashvars" value="autostart=true&amp;file=myvideo.swf" />
				</object>
				当前浏览器不支持 video直接播放，点击这里下载视频： <a href="myvideo.webm">下载视频</a>
			</video>
		</div>
		<div class="comment">
			用户评论:
			<div class="comments">
				<div class="comment-detail" v-for="x in comments">
					<div class="people">
						<div class="header">
							<img :src="x.photo"/>
						</div>
						<div class="username">
							{{x.username}}：
						</div>
					</div>
					<div class="content">
						{{x.comment_contant}}
					</div>
				</div>
			</div>
			<div class="takeComment">
				<div class="" v-if="canComment">
					<el-input
					  type="textarea"
					  :rows="2"
					  placeholder="请输入评论内容"
					  v-model="commentDetail">
					</el-input>
					<div class="submit_div">
						<el-button type="primary" @click="saveComment">提交</el-button>
					</div>
				</div>
				<div class="" v-else>
					请登陆
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				commentDetail:'',
				game:{},
				imgLength:false,
				comments:[],
				canComment:localStorage.getItem('userId')?true:false
			}
		},
		mounted(){
			console.log(this.canComment)
			apiMapper.getGameInfo({
				gameId:this.$route.query.id
			}).then(data=>{
				let res = data.data.data;
				if(res.photoes){
					res.photoes = res.photoes.split(',')
					this.imgLength = true;
				}else{
					res.photoes = [];
					this.imgLength = false;
				}
				if(res.download_urls){
					res.download_urls = res.download_urls.split(',')
				}else{
					res.download_urls = [];
				}
				if(res.urls){
					res.urls = res.urls.split(',')
				}else{
					res.urls = [];
				}
				this.game = res;
				this.getCommentByGameId();
			})
		},
		methods:{
			getCommentByGameId(){
				apiMapper.getCommentByGameId({
					gameId:this.$route.query.id
				}).then(data=>{
					this.comments = data.data.data;
				})
			},
			saveComment(){
				apiMapper.saveComment({
					content:this.commentDetail,
					gameId:this.$route.query.id,
					commentUserId:localStorage.getItem('userId')
				}).then(data=>{
					this.commentDetail = '';
					this.getCommentByGameId();
				})
			}
		}
	}
</script>

<style scoped>
	.detail-page{
		font-size: 20px;
		background: white;
		opacity: .8;
	}
	.photoes>div{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	.photoes>div>img{
		margin-bottom: 20px;
	}
	.video{
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.people{
		display: flex;
	    justify-content: flex-start;
	    align-items: center;
	}
	.header{
		width: 50px;
		height: 50px;
		background: white;
		border-radius: 50%;
		overflow: hidden;
		display: flex;
		justify-content: center;
		align-items: center;
		border: 1px solid #DDDDDD;
	}
	.username{
		margin-left: 20px;
	}
	.content{
		margin-left: 70px;
	}
	.comments{
		margin: 20px;
	}
	.takeComment{
		width: 80vw;
		height: 200px;
		margin: 0 auto;
	}
	.takeComment>div{
		display: flex;
	    justify-content: flex-start;
	    align-items: center;
	    flex-direction: row;
	}
	.submit_div{
		margin-left: 50px;
	}
</style>


