<template>
	<div class="typeManage-page">
		<div class="add-comp">
			<el-button type="primary" @click="addType">添加类型</el-button>
		</div>
		<data-list 
			:headers="headers" 
			:action="actions" 
			:data="data"
			@detail="detail"
			@remove="remove"
			>
		</data-list>
		<my-dialog :showFlag="showFlag" @closeDialog="closeDialog" name="类型详情">
			<detail :data="detailData" :form="detailForm" @submit="submit"></detail>
		</my-dialog>
		
		<el-dialog
		  title="类型添加"
		  :visible.sync="dialogVisible"
		  width="500px"
		  >
		  <div class="">
		  	<el-form ref="addForm" :model="addForm" label-width="80px" :rules="rules"> 
			<el-form-item label="类型名称" prop="typeName">
				<el-input v-model="addForm.typeName"></el-input>
			</el-form-item>
			<el-form-item label="备注">
				<el-input v-model="addForm.remark"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitAdd">保存</el-button>
				<el-button @click="cancelAdd">取消</el-button>
			</el-form-item>
		</el-form>
		  </div>
</el-dialog>
		
	</div>
</template>

<script>
	import dataList from '@/components/manage/dataTable'
	import myDialog from '@/components/manage/dialog'
	import detail from '@/components/manage/detail'
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				headers:[
					{
						label:'类型名称',
						prop:'type_name'
					},{
						label:'创建时间',
						prop:'create_time'
					},{
						label:'创建人',
						prop:'real_name'
					}
				],
				actions:['detail','remove'],
				data:[],
				dialogVisible:false,
				showFlag:0,
				detailData:[
					{
						label:'类型名称',
						prop:'type_name',
						type:'input'
					},{
						label:'备注',
						prop:'remark',
						type:'input'
					},{
						label:'创建时间',
						prop:'create_time',
						type:'text'
					},{
						label:'头像',
						prop:'photo',
						type:'text'
					},{
						label:'创建人',
						prop:'real_name',
						type:'text'
					},{
						label:'手机号',
						prop:'phone',
						type:'text'
					},{
						label:'QQ',
						prop:'qq',
						type:'text'
					},{
						label:'Email',
						prop:'email',
						type:'text'
					}
				],
				detailForm:{},
				addForm:{
					typeName:'',
					remark:''
				},
				rules:{
					typeName: [{
						required: true,
						message: '请输入类型名称',
						trigger: 'blur'
					}]
				}
			}
		},
		mounted(){
			this.getList();
		},
		methods:{
			getList(){
				apiMapper.queryType().then( data => {
					this.data = data.data.data; 
				})
			},
			detail(val){
				this.detailForm = val;
				this.showFlag = 1;
			},
			remove(val){
				apiMapper.removeType({
					typeId:val.type_id
				}).then(data=>{
					this.$message.success('删除成功');
					this.getList();
				})
			},
			closeDialog(){
				this.showFlag = 2;
			},
			submit(val){
				apiMapper.updateType({
					typeName:val.type_name,
					remark:val.remark,
					typeId:val.type_id
				}).then(data=>{
					this.getList();
					this.closeDialog();
				})
			},
			submitAdd(){
				apiMapper.saveType({
					typeName:this.addForm.typeName,
					remark:this.addForm.remark,
 					userId:sessionStorage.getItem('userId')
				}).then(data=>{
					this.$message.success('添加成功')
					this.cancelAdd();
					this.getList();
				})
			},
			cancelAdd(){
				this.dialogVisible = false;
			},
			addType(){
				this.dialogVisible = true;
			},
		},
		components:{
			dataList,
			detail,
			myDialog
		}
	}
</script>

<style scoped>
	.add-comp{
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin-bottom: 40px;
	}
</style>


