<template>
	<div class="managerManage-page">
		<div class="add-comp">
			<el-button type="primary" @click="addManager">添加管理员</el-button>
		</div>
		<data-list :headers="headers" :action="actions" :data="data" @remove="remove">
		</data-list>

		<el-dialog title="管理员添加" :visible.sync="dialogVisible" width="500px">
			<div class="">
				<el-form ref="addForm" :model="addForm" label-width="80px" :rules="rules">
					<el-form-item label="账号" prop="username">
						<el-input v-model="addForm.username"></el-input>
					</el-form-item>
					<el-form-item label="密码" prop="password">
						<el-input v-model="addForm.password"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" @click="submitAdd">保存</el-button>
						<el-button @click="cancelAdd">取消</el-button>
					</el-form-item>
				</el-form>
			</div>
		</el-dialog>

	</div>
</template>

<script>
	import dataList from '@/components/manage/dataTable'
	import myDialog from '@/components/manage/dialog'
	import detail from '@/components/manage/detail'
	import { apiMapper } from '@/axios/axios'
	export default {
		data() {
			return {
				headers: [{
					label: '账号',
					prop: 'username'
				}, {
					label: '密码',
					prop: 'password'
				}],
				actions: ['remove'],
				data: [],
				page: 1,
				total: 0,
				username: '',
				dialogVisible: false,
				addForm: {
					username: '',
					password: ''
				},
				rules: {
					username: [{
						required: true,
						message: '请输入账号',
						trigger: 'blur'
					}],
					password: [{
						required: true,
						message: '请输入密码',
						trigger: 'blur'
					}]
				}
			}
		},
		mounted() {
			this.getList();
		},
		methods: {
			getList() {
				apiMapper.getUserList({
					username: this.username,
					page: this.page,
					limit: 10
				}).then(data => {
					this.data = data.data.data.data;
					this.total = data.data.data.total;
				})
			},
			remove(val) {
				apiMapper.removeUser({
					userId: val.user_id
				}).then(data => {
					this.$message.success('删除成功');
					this.getList();
				})
			},
			submitAdd() {
				apiMapper.saveManager({
					username: this.addForm.username,
					password: this.$md5(this.addForm.password)
				}).then(data => {
					this.$message.success('添加成功');
					this.cancelAdd();
				})
			},
			addManager() {
				this.dialogVisible = true;
			},
			cancelAdd() {
				this.dialogVisible = false;
			},
		},
		components: {
			dataList,
			detail,
			myDialog
		}
	}
</script>

<style scoped>
	.add-comp {
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}
</style>


