<template>
	<div class="dialog-comp-box" v-if="showDialogFlag">
	<div class="hideBox" @click="hideDialogComp" >
	</div>
	<div class="dialog-comp" :class="[show==1?'showAnimate':show==2?'hideAnimate':'']">
		<div class="dialog-box">
			<div class="dialog-title">
			<div class="title-name">
				{{name}}
			</div>	
			<div class="title-cancel">
				<i class="el-icon-close closeBtn" @click="closeDialog"></i>
			</div>
		</div>
		<div class="dialog-content">
			<slot></slot>
		</div>
		</div>
	</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				show:0,
				showDialogFlag:false,
			}
		},
		props:{
			name:{
				type:String,
				default:'test'
			},
			showFlag:{
				type:Number,
				default:0
			}
		},
		watch:{
			showFlag(v){
				this.show = v;
				if(v==1){
					this.showDialogFlag = true;
				}else{
					setTimeout(()=>{
						this.showDialogFlag = false;
					},300)
				}
			}
		},
		mounted(){
			this.show = this.showFlag
		},
		methods:{
			closeDialog(){
				this.$emit('closeDialog');
			},
			hideDialogComp(){
				this.closeDialog();
			},
		}
	}
</script>

<style scoped>
	.dialog-comp-box{
		top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    overflow: auto;
	    margin: 0;
	    z-index: 100;
	    position: fixed;
	}
	.dialog-comp{
		position: fixed;
		background: white;
		top: 0;
		right: -400px;
		z-index: 4;
		width: 400px;
		height: 100vh;
		border: 1px solid #DDDDDD;
	}
	.dialog-box{
		padding: 20px;
	}
	.dialog-title{
		display: flex;
	    justify-content: space-between;
	    align-items: center;
	    line-height: 50px;
	    font-size: 20px;
	}
	.closeBtn{
		cursor: pointer;
	}
	.showAnimate{
		box-shadow:1px 5px 10px 3px rgba(169, 185, 179, 0.6);
		animation:showAnimate .3s linear 0s 1 alternate;
		animation-fill-mode: forwards;	
		overflow: auto;
	}
	@keyframes showAnimate{
		from{right:-400px;}
		to{right:0;}
	}
	.hideAnimate{
		animation:hideAnimate .3s linear 0s 1 alternate;
		animation-fill-mode: forwards;	
	}
	@keyframes hideAnimate{
		from{right:0px;}
		to{right:-400px;}
	}
	.hideBox{
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    overflow: auto;
	    margin: 0;
	    width: 100vw;
	    height: 100vh;
	    z-index: 3;
	}
</style>


