<template>
	<div class="login-page">
		<div class="login-window">
			<div class="login-window-box">
				<el-tabs v-model="activeName" stretch>
				    <el-tab-pane label="用户登陆" name="first">
						<login @login="login"></login>
				    </el-tab-pane>
				    <el-tab-pane label="注册账号" name="second">
				    	<register @register="register"></register>
				    </el-tab-pane>
				  </el-tabs>
			</div>
		</div>
	</div>
</template>

<script>
	import login from '@/components/net/login'
	import register from '@/components/net/register'
	import {apiMapper} from '@/axios/axios'
	export default {
		data() {
			return {
				activeName:'first',
			}
		},
		components:{
			login,
			register
		},
		methods:{
			register(data){
				apiMapper.saveUser({
					username:data.username,
					password:this.$md5(data.pwd)
				}).then(res=>{
					if(res.data.code !=0){
						this.$message.error(res.data.msg)	
					}else{
						this.cache(res.data.data.user_id);
						localStorage.setItem('username',data.username)	
					}
				})
			},
			login(data){
				apiMapper.userLogin({
					username:data.username,
					password:this.$md5(data.pwd)
				}).then(res=>{
					if(res.data.code !=0){
						this.$message.error(res.data.msg)	
					}else{
						this.cache(res.data.data.user_id);
						localStorage.setItem('username',res.data.data.username)	
					}
				})
			},
			cache(id){
				localStorage.setItem('userId',id)	
				this.$store.state.userId = id;
				apiMapper.getUserInfo({
					userId:id
				}).then(data=>{
					localStorage.setItem('userphoto',data.data.data.photo);
					history.go(-1);
				})
			}
		}
	}
</script>

<style scoped>
	.login-page{
		/*width: 100vw;
		height: 100vh;
		background: url(../../assets/img/loginBg.jpg) no-repeat center;*/
	}
	.login-window {
		width: 400px;
		height: 500px;
		border: 1px solid #31C27C;
		display: flex;
		justify-content: center;
		align-items: center;
		background: white;
	    position: absolute;
	    top: 8vh;
	    left: 40vw;
	}
	
	.login-window-box {
		height: 400px;
		position: relative;
		right: 30px;
	}
</style>


