<template>
	<div class="detail-comp">
		<el-form ref="form" :model="map_form" label-width="80px">
			<el-form-item :label="x.label" v-for="(x,index) in data" :key="index">
				<el-input v-if="x.type=='input'" v-model="map_form[x.prop]"></el-input>
				<div v-if="x.type=='text'" class="">
					<span v-if="x.prop!='photo'">{{map_form[x.prop]}}</span>
					<div v-else>
						<img class='headerImg' :src="map_form.photo" v-if="map_form.photo" />
						<div v-else class="defaultImg">
							暂无
						</div>
					</div>
				</div>
				<el-select v-if="x.type=='select'" v-model="map_form[x.prop]" placeholder="请选择">
				    <el-option
				      v-for="item in childData"
				      :key="item.type_id"
				      :label="item.type_name"
				      :value="item.type_id">
				    </el-option>
				  </el-select>
				<el-upload
					v-if="x.type=='upload' && x.prop=='photoes'"
				  :action="uploadUrl"
				  :file-list='fileList'
				  list-type="picture-card"
				  :on-success="handleAvatarSuccess"
				  :on-remove="handleRemove">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-upload
					v-if="x.type=='upload' && x.prop=='video'"
				  class="upload-demo"
				  drag
				  :on-success="handleVideoSuccess"
				  :action="uploadUrl"
				  >
				  <i class="el-icon-upload"></i>
				  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				  <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
				</el-upload>
			</el-form-item>
			<el-form-item v-if="showBtn">
				<el-button type="primary" @click="submit">保存</el-button>
				<el-button>取消</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import env from '@/axios/env.js';
	export default {
		data() {
			return {
				map_form: {},
				uploadUrl:'',
				fileList:[],
			}
		},
		mounted() {
			this.uploadUrl = `${env}upload`;
			this.map_form = JSON.parse(JSON.stringify(this.form));
			if(this.map_form.photoes){
				if(this.map_form.photoes.includes(',')){
					let arr = this.map_form.photoes.split(',');
					arr.map(v =>{
						this.fileList.push({
							name:'name',
							url:v
						})
					})
				}else{
					this.fileList = [{
						name:'name',
						url:this.map_form.photoes
					}];
				}
			}
		},
		props: {
			data: {
				type: [Object, Array],
			},
			form: {
				type: Object,
				default: {}
			},
			showBtn: {
				type: Boolean,
				default: true
			},
			childData:{
				type:[Object,Array]
			}
		},
		methods: {
			submit() {
				this.map_form.photoes = [];
				this.fileList.map(v=>{
					this.map_form.photoes.push(v.url); 
				})
				this.$emit('submit', this.map_form);
			},
			handleAvatarSuccess(res, file){
				this.fileList.push({
					name:'name',
					url:`${env+res.data.path}`
				})
			},
			handleRemove(file, fileList){
				this.fileList=fileList;
			},
			handleVideoSuccess(res, file){
				this.map_form.video=`${env+res.data.path}`;
			}
		}
	}
</script>

<style scoped>
	.defaultImg,
	.headerImg {
		width: 40px;
		height: 40px;
		border-radius: 50%;
		border: 1px solid #DDDDDD;
		background: #409EFF;
		display: flex;
		justify-content: center;
		align-items: center;
		color: white;
	}
</style>


