<template>
	<div class="home-page">
			<rank :data="hotRank" @toDetail="toDetail"></rank>
			<div class="content">
				<div class="typeList" v-for="(x,index) in typeArr">
					<span>{{x}}</span>
					<div class="gameList-box">
						<div class="gameList" v-for="(y,index2) in gameList" v-if="y.type_name == x" @click="jumpToDetail(y.game_id)">
							<img :src="y.photoes[0]" v-if="y.photoes"/>
							<div class="defaultImg" v-else>
							</div>
							<span class="gameList-name">{{y.game_name}}</span>
						</div>
					</div>
				</div>
			</div>
			
	</div>
</template>

<script>
	import {apiMapper} from '@/axios/axios'
	import rank from '@/components/net/rank'
	export default{
		data(){
			return{
				typeArr:[],
				gameList:[],
				hotRank:[],
				options:{
					gameName:"",
				    typeId:"",
				    userName:"",
				    startTime:"",
				    endTime:"",
				    page:1,
				    limit:10
				},
				hotOptions:{
					page:1,
					limit:10
				}
			}
		},
		mounted(){
			let arr = [];
				apiMapper.getGameList(this.options).then(data =>{
					let res = data.data.data.data;
					res.map(v=>{
						arr.push(v.type_name);
						if(v.photoes){
							v.photoes = v.photoes.split(',');
						}else{
							v.photoes = [];
						}
					})
					let set = new Set(arr);
					this.typeArr = Array.from(set);
					this.gameList = res;
				})
				apiMapper.getHotGame(this.hotOptions).then(data =>{
					this.hotRank = data.data.data; 
				})
		},
		methods:{
			jumpToDetail(id){
				this.$router.push({
					path:'/detail',
					query:{
						id:id
					}
				})
			},
			toDetail(val){
				this.jumpToDetail(val.game_id)
			}
		},
		components:{
			rank
		}
	}
</script>

<style scoped>
	.home-page{
		padding: 50px 0;
	}
	.content{
		width: 80%;
		min-height: 82vh;
		background: white;
		border-left: 1px solid #DDDDDD;
		border-right: 1px solid #DDDDDD;
		margin: auto;
		opacity: .8;
	}
	.gameList-box{
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	.typeList{
		width: 100%;
		overflow-x: auto;
	}
	.typeList>span{
		font-size: 20px;
		font-weight: 600;
		margin: 10px;
	}
	.gameList{
		width: 100px;
		height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin:0 10px 10px;
		flex-direction: column;
		cursor: pointer;
	}
	.defaultImg{
		width: 50px;
		height: 50px;
		background: #565A7D;
		border-radius: 10px;
	}
	.gameList>img{
		width: 50px;
		height: 50px;
		border-radius: 10px;
	}
	.gameList-name{
		width: 100%;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
		text-align: center;
	}
</style>


