import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/net/index'
import Home from '@/views/net/home'
import Login from '@/views/net/login'
import Detail from '@/views/net/detail'
import Setting from '@/views/net/setting'

import Manage from '@/views/manage/index'
import ManageLogin from '@/views/manage/login'
import userManage from '@/views/manage/userManage'
import managerManage from '@/views/manage/managerManage'
import gameManage from '@/views/manage/gameManage'
import typeManage from '@/views/manage/typeManage'
import commentManage from '@/views/manage/commentManage'


Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      redirect:'/',
      children:[
      	{
      		path:'/',
      		name:'home',
      		meta: {
						requiresAuth: false
					},
      		component:Home
      	},
      	{
      		path:'/detail',
      		name:'detail',
      		meta: {
						requiresAuth: false
					},
      		component:Detail
      	},{
      		path:'/setting',
      		name:'setting',
      		meta: {
						requiresAuth: true
					},
      		component:Setting
      	}
      ]
    },
    {
      path: '/login',
      name: 'login',
      meta: {
				requiresAuth: false
			},
      component: Login
    },
    {
      path:'/manage',
      name:'manage',
      component:ManageLogin
    },
    {
      path: '/manageHome',
      name: 'manageHome',
      component: Manage,
      redirect:'/userManage',
      children:[
      	{
      		path:'/userManage',
      		name:'userManage',
      		component:userManage
      	},{
      		path:'/managerManage',
      		name:'managerManage',
      		component:managerManage
      	},{
      		path:'/gameManage',
      		name:'gameManage',
      		component:gameManage
      	},{
      		path:'/typeManage',
      		name:'typeManage',
      		component:typeManage
      	},{
      		path:'/commentManage',
      		name:'commentManage',
      		component:commentManage
      	}
      ]
    },
  ]
})


router.beforeEach((to, from, next) => {
		//判断是否需要鉴权;
		if(to.meta.requiresAuth) {
			let userId= localStorage.getItem('userId');
			if(!userId) {
				next({
					name: 'login',
					query: {
						redirect: to.fullPath
					}
				})
			} else {
				next();
			}
		} else {
			next();
		}		
})
export default router;


