<template>
	<div class="home-page">
		<my-header>
			<div class="username" v-if="$store.state.username">
				<el-popover
				  placement="bottom"
				  width="100"
				  trigger="hover">
				  <div class="child" @click="setting">个人设置</div>
				  <div class="child noBorder" @click="loginUp">退出</div>
				  <div class="user" slot="reference">
			    	<div class="user-header">
			    		<img :src="$store.state.photo" v-if="$store.state.photo"/>
			    		<span v-else>{{$store.state.username.slice(-1)}}</span>
			    	</div>  	
			    	<div class="user-name">
			    		{{$store.state.username}}
			    	</div>
			      </div>
				</el-popover>
			</div>
			<div class="login" @click="jumpToLogin" v-else>
				登陆	
			</div>
		</my-header>
		<main>
			<router-view></router-view>
		</main>
	</div>
</template>

<script>
	import myHeader from '@/components/net/header'
	export default{
		data(){
			return{
				
			}
		},
		components:{
			myHeader
		},
		watch:{
//			'$route' (to, from) {
//		     	if(to.name =='setting'){
//		     		if(){}
//		     	}
//		    }
		},
		mounted(){
			this.$store.state.username = localStorage.getItem('username');
			this.$store.state.photo = localStorage.getItem('userphoto');
		},
		methods:{
			loginUp(){
				localStorage.clear();
				this.$store.state.username = '';
				if(this.$route.name =='setting'){
					this.$router.push({
						path:'/'
					})
				}
			},
			setting(){
				console.log('个人设置')
				this.$router.push({
					path:'/setting'
				})
			},
			jumpToLogin(){
				localStorage.getItem('userId')
				this.$router.push({
					path:'/login'
				})
			}
		}
	}
</script>

<style scoped>
	.username,.login{
		margin-right: 50px;
		cursor: pointer;
	}
	.user{
		display: flex;
		justify-content: flex-start;
		align-items: center;
		cursor: pointer;
	}
	.user-header{
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background: #B3C0D1;
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;
	}
	.user-header>img{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.child{
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 10px 0;
		border-bottom: 1px solid #DDDDDD;
		cursor: pointer;
	}
	.noBorder{
		border-bottom: 0px;
		color: red;
	}
</style>


