<template>
	<div class="rank-comp">
		<div class="rank-box" :class="[show==1?'animateShow':show==2?'animateHide':'']">
			<div class="rank-title">
				
			</div>
			<div class="game" v-for="(x,index) in data" @click="jumpToDetail(x)">
				<span class="rankNumber">{{index+1}}</span>.{{x.game_name}}
			</div>
			<div class="RankTag" @click="changeShow">
				{{show==1?'排行榜':'排行榜'}}
			</div>
		</div>		
	</div>
</template>

<script>
	export default{
		data(){
			return{
				show:0,			//1慢出,2慢入,0 隐藏
				games:[
					{
						name:'剑网三'
					},{
						name:'剑网三'
					},{
						name:'剑网三'
					},{
						name:'剑网三'
					},{
						name:'剑网三'
					},{
						name:'剑网三'
					},
				]
			}
		},
		props:{
			data:{
				type:[Object,Array]
			}
		},
		methods:{
			jumpToDetail(val){
				this.$emit('toDetail',val);
			},
			changeShow(){
				if(this.show == 0){
					this.show = 1;	
				}else{
					this.show = 3-this.show;
				}
				
			}
		}
	}
</script>

<style scoped>
	.rank-comp{
		position: absolute;
		color: #6C7EC0;
	}
	.rank-box{
		width: 150px;
		max-height: 90vh;
		position: relative;
		left: -150px;
		top: -6vh;
		background: white;
		z-index: 10;
	}
	.rank-title{
		text-align: center;
	}
	.game{
		cursor: pointer;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 25px 0 ;
	}
	.RankTag{
		width: 100px;
		height: 50px;
		background: white;
		position: absolute;
		z-index: 1;
		top: 20vh;
		right: -100px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
	}
	.animateShow{
		animation:animateShow .3s linear 0s 1 alternate;
		animation-fill-mode: forwards;	
	}
	@keyframes animateShow{
		from{left: -150px;}
		to{left: 0;}
	}
	.animateHide{
		animation:animateHide .3s linear 0s 1 alternate;
		animation-fill-mode: forwards;
	}
	@keyframes animateHide{
		from{left: 0;}
		to{left: -150px;}
	}
	.rankNumber{
		font-size: 25px;
	}
</style>


