import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({
	state:{
		username:localStorage.getItem('username')||'',
		photo:localStorage.getItem('userphoto')||''
	},
	getters:{
		username: state => state.username
	},
	mutations:{},
	modules:{
	}
})

export default store;


