<!--
	列表组件
	Attributes:
		headers: Object 表头信息，需要配置{label(名称，必须) ,prop(属性名，必须),action(操作,映射为actions里面的操作,选填)}  ;
		action: Array 操作内容 分为 remove(删除) , info(查看) ,detail(详情) ,status(状态切换);
	Events:
		handleSelectionChange(array)	//选中的数据
		info(val);
		remove(val);
		detail(val);
		status(val);
		返回值是操作的那行数据;
-->
<template>
	<div class="dataTable-comp">
		<el-table :data="data" border style="width: 100%" header-row-class-name="table-header" @selection-change="handleSelectionChange">
			<el-table-column v-if="isSelection" type="selection" width="55">
			</el-table-column>
			<el-table-column :prop="x.prop" :label="x.label" v-for="(x,index) in headers" :key='index'>
				<template slot-scope="scope">
					<span v-if="x.prop!='status'" :class="[x.action?'acitve':'']" @click="canlink(scope.row,x.action)">{{ scope.row[x.prop]}}</span>
					<span v-if="x.prop=='status'">{{ scope.row['status']==1?'已启用':'已停用'}}</span>
				</template>
			</el-table-column>
			<el-table-column label="操作">
				<template slot-scope="scope">
					<el-tooltip class="item" effect="light" content="基本信息" placement="top" popper-class="info-tips">
					    <span class="actionspan info" v-if="action.includes('info')" @click="info(scope.row)"></span>
					</el-tooltip>
					<el-tooltip class="item" effect="light" content="详情" placement="top" popper-class="detail-tips">
					    <span class="actionspan detail" v-if="action.includes('detail')" @click="detail(scope.row)"></span>
					</el-tooltip>
					<el-tooltip v-if="scope.row.status==1" class="item" effect="light" content="停用" placement="top" popper-class="status-tips">
					    <span class="actionspan status" v-if="action.includes('status')" @click="status(scope.row)"></span>
					</el-tooltip>
					<el-tooltip v-if="scope.row.status==0" class="item" effect="light" content="启用" placement="top" popper-class="status-use-tips">
					    <span class="actionspan status-use" v-if="action.includes('status')" @click="status(scope.row)"></span>
					</el-tooltip>
					<el-tooltip class="item" effect="light" content="删除" placement="top" popper-class="delete-tips">
					    <span class="actionspan delete" v-if="action.includes('remove')" @click="remove(scope.row)"></span>
					</el-tooltip>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				
			}
		},
		props:{
			headers:{
				type:[Object,Array]
			},
			data:{
				type:[Object,Array]
			},
			action:{
				type:Array
			},
			isSelection:{
				type:Boolean,
				default:false
			}
		},
		methods:{
			handleSelectionChange(val){
				this.$emit('handleSelectionChange',val);
			},
			canlink(val,flag){
				if(flag){
					this[flag]();
				}else{
					return false;
				}
			},
			info(val){
				this.$emit('info',val);
			},
			detail(val){
				this.$emit('detail',val);
			},
			status(val){
				this.$emit('status',val);
			},
			remove(val){
				this.$emit('remove',val);
			}
		}
	}
</script>

<style scoped>
	.acitve{
		color: #58d9aa;
		cursor: pointer;
	}
	.actionspan{
		color: #2ED095;
	    cursor: pointer;
	    display: inline-block;
	    height: 30px;
	    min-width: 30px;
	    outline: none;
	}	
	.info{
		background: url(../../assets/img/icon_view_grey.png) no-repeat center;
	}
	.info:hover{
		background: url(../../assets/img/icon_view_green.png) no-repeat center;
	}
	.detail{
		background: url(../../assets/img/icon_detail_grey.png) no-repeat center;
	}
	.detail:hover{
		background: url(../../assets/img/icon_detail_green.png) no-repeat center;
	}
	.status{
		background: url(../../assets/img/icon_ban_grey.png) no-repeat center;
	}
	.status:hover{
		background: url(../../assets/img/icon_ban_orange.png) no-repeat center;
	}
	.status-use{
		background: url(../../assets/img/icon_use_grey.png) no-repeat center;
	}
	.status-use:hover{
		background: url(../../assets/img/icon_use_green.png) no-repeat center;
	}
	.delete{
		background: url(../../assets/img/icon_delete_grey.png) no-repeat center;
	}
	.delete:hover{
		background: url(../../assets/img/icon_delete_orange.png) no-repeat center;
	}
	.table-header{
		background: red;
	}
</style>


