<template>
	<div class="header-comp">
		<div class="logo" @click="jumpToIndex">
			<img src="../../assets/img/logo.jpg"/>
			<span>GC Game</span>
		</div>
		<slot>登陆</slot>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				
			}
		},
		methods:{
			jumpToIndex(){
				this.$router.push({
					path:'/'
				})
			}
		}
	}
</script>

<style scoped lang='scss'>
	.header-comp{
		width: 100%;
		height:70px;
		background: #565A7D;
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: white;
	}
	.logo{
		    display: flex;
    	align-items: center;
    	cursor: pointer;
	}
	.logo>img{
		width: 50px;
		height: 50px;
		object-fit: cover;
		border-radius: 50%;
		overflow: hidden;
		margin:0 20px 0 50px;
	}
</style>


