const hostname = location.hostname;

const NODE_ENV = {
	develop: 'http://47.106.214.103:3009/',
	test:'http://api-yxyp.test.anoah.com/front/',
	prod:'http://localhost:3000/',
	mock:'http://yapi.dev.anoah.com/mock/97/',
	xujian:'http://192.168.45.28:8002/',
	szp:'http://192.168.45.66:8002/',
	dev:'http://192.168.46.90:8002/',//statistic
	cr:'http://192.168.45.48:8002/',
	wg:'http://192.168.45.25:9000/front/',
};


let settingEnv = (hostname)=>{
	if (sessionStorage.getItem('apiHost')) {
		return sessionStorage.getItem('apiHost')
	}else{
		switch (hostname){
			case 'localhost':
				return NODE_ENV.prod
				break;
			case 'yxyp.dev.anoah.com':
				return NODE_ENV.develop
				break;
			case 'yxyp.test.anoah.com':
				return NODE_ENV.test
				break;
			case 'yxyp.anoah.com':
				return NODE_ENV.prod
				break;
			default:
				return NODE_ENV.develop
				break;
		}
	}	
};

export default settingEnv(hostname)



