<template>
	<div class="setting-page">
		<el-form ref="form" :model="form" label-width="80px">
			<el-form-item label="用户头像">
				<el-upload
				  class="avatar-uploader"
				  :action="uploadUrl"
				  :show-file-list="false"
				  :on-success="handleAvatarSuccess"
				  :before-upload="beforeAvatarUpload">
				  <img v-if="form.photo" :src="form.photo" class="avatar">
				  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
				</el-upload>
			</el-form-item>
			<el-form-item label="用户姓名">
				<el-input v-model="form.real_name"></el-input>
			</el-form-item>
			<el-form-item label="手机号">
				<el-input v-model="form.phone"></el-input>
			</el-form-item>
			<el-form-item label="居住地址">
				<el-input v-model="form.address"></el-input>
			</el-form-item>
			<el-form-item label="QQ">
				<el-input v-model="form.qq"></el-input>
			</el-form-item>
			<el-form-item label="邮箱地址">
				<el-input v-model="form.email"></el-input>
			</el-form-item>
			<el-form-item>
				<div class="btns">
					<el-button type="primary" @click="submit">保存</el-button>
					<el-button @click="cancel">取消</el-button>
				</div>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import env from '@/axios/env.js';
	import {apiMapper} from '@/axios/axios'
	export default {
		data() {
			return {
				form:{
					real_name:'',
					photo:'',
					phone:'',
					address:'',
					qq:'',
					email:''
				},
				uploadUrl:''
			}
		},
		mounted(){
			this.uploadUrl = `${env}upload`;
			apiMapper.getUserInfo({
				userId:localStorage.getItem('userId')
			}).then(data=>{
				for(let key in this.form){
					this.form[key] = data.data.data[key];
				}
			})
		},
		methods:{
			handleAvatarSuccess(res, file){
				if(res.code==0){
					this.form.photo = `${env+res.data.path}`;
				}
			},
			beforeAvatarUpload(file){
				
			},
			submit(){
				apiMapper.updateInfo({
					userId:localStorage.getItem('userId'),
					realName:this.form.real_name,
					photo:this.form.photo,
					phone:this.form.phone,
					address:this.form.address,
					qq:this.form.qq,
					email:this.form.email
				}).then(data=>{
					if(data.data.code==0){
						this.$store.state.photo = this.form.photo;
						localStorage.setItem('userphoto',this.form.photo);
						this.$message.success('修改成功')
					}
				})
			},
			cancel(){
				history.go(-1);
			}	
		}
	}
</script>

<style scoped>
	.setting-page{
	   width: 300px;
    background: white; 
    margin: auto;
    padding: 30px 50px;
    opacity: .8;
    margin-top: 20px;
	}
 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
  	font-size: 28px;
    color: #8c939d;
    width: 80px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    overflow: hidden;
    object-fit: cover;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .btns{
  	margin-left: 80px;
  }
</style>


