<template>
	<div class="commentManage-page">
		<data-list 
			:headers="headers" 
			:action="actions" 
			:data="data"
			@detail="detail"
			@remove="remove"
			>
		</data-list>
		<my-dialog :showFlag="showFlag" @closeDialog="closeDialog" name="评论详情">
			<detail :data="detailData" :form="detailForm" @submit="submit" :showBtn="false"></detail>
		</my-dialog>
	</div>
</template>

<script>
	import dataList from '@/components/manage/dataTable'
	import myDialog from '@/components/manage/dialog'
	import detail from '@/components/manage/detail'
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				headers:[
					{
						label:'评论内容',
						prop:'comment_contant'
					},{
						label:'评论时间',
						prop:'comment_create_time'
					},{
						label:'评论者',
						prop:'username'
					}
				],
				actions:['detail','remove'],
				data:[],
				showFlag:0,
				detailData:[
					{
						label:'游戏名称',
						prop:'game_name',
						type:'text'
					},{
						label:'热度',
						prop:'hot',
						type:'text'
					},{
						label:'评论时间',
						prop:'comment_create_time',
						type:'text'
					},{
						label:'评论内容',
						prop:'comment_contant',
						type:'text'
					},{
						label:'评论者',
						prop:'username',
						type:'text'
					}
				],
				detailForm:{},
				options:{
					content:"",
				    startTime:"",
				    endTime:"",
				    page:1,
				    limit:10
				}
			}
		},
		mounted(){
			this.getList();
		},
		methods:{
			getList(){
				apiMapper.getCommentList(this.options).then( data => {
					this.data = data.data.data.data; 
				})
			},
			detail(val){
				this.detailForm = val;
				this.showFlag = 1;
			},
			remove(val){
				apiMapper.removeComment({
					commentId:val.comment_id
				}).then(data=>{
					this.$message.success('删除成功');
					this.getList();
				})
			},
			closeDialog(){
				this.showFlag = 2;
			},
			submit(val){
				apiMapper.updateType({
					typeName:val.type_name,
					remark:val.remark,
					typeId:val.type_id
				}).then(data=>{
					this.getList();
					this.closeDialog();
				})
			},
		},
		components:{
			dataList,
			detail,
			myDialog
		}
	}
</script>

<style scoped>
</style>


