<template>
	<div class="gameManage-page">
		<div class="add-comp">
			<el-button type="primary" @click="addNewGame">添加游戏</el-button>
		</div>
		<data-list 
			:headers="headers" 
			:action="actions" 
			:data="data"
			@detail="detail"
			@remove="remove"
			>
		</data-list>
		<my-dialog :showFlag="showFlag" @closeDialog="closeDialog" name="游戏详情">
			<detail :data="detailData" :form="detailForm" @submit="submit" :childData='dataType'></detail>
		</my-dialog>
	</div>
</template>

<script>
	import dataList from '@/components/manage/dataTable'
	import myDialog from '@/components/manage/dialog'
	import detail from '@/components/manage/detail'
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				headers:[
					{
						label:'名称',
						prop:'game_name'
					},{
						label:'热度',
						prop:'hot'
					},{
						label:'类型',
						prop:'type_name'
					}
				],
				actions:['detail','remove'],
				data:[],
				dataType:[],
				showFlag:0,
				detailData:[],
				detailForm:{},
				options:{
					gameName:"",
				    typeId:"",
				    userName:"",
				    startTime:"",
				    endTime:"",
				    page:1,
				    limit:10
				}
			}
		},
		mounted(){
			this.getType();
		},
		methods:{
			getType(){
				apiMapper.queryType().then( data => {
					this.dataType = data.data.data;
					this.getList();
				})
			},
			getList(){
				apiMapper.getGameList(this.options).then(data=>{
					this.data = data.data.data.data;
				})
				
			},
			detail(val){
				this.detailForm = val;
				this.detailData = [
					{
						label:'游戏类型',
						prop:'type_id',
						type:'select',
					},
					{
						label:'游戏名称',
						prop:'game_name',
						type:'input'
					},{
						label:'描述',
						prop:'abstract',
						type:'input'
					},{
						label:'详情',
						prop:'detaile',
						type:'input'
					},{
						label:'热度',
						prop:'hot',
						type:'text'
					},{
						label:'下载地址',
						prop:'download_urls',
						type:'input'
					},{
						label:'创建时间',
						prop:'game_create_time',
						type:'text'
					},{
						label:'展示图片',
						prop:'photoes',
						type:'upload'
					},{
						label:'友情链接',
						prop:'urls',
						type:'input'
					},{
						label:'视频信息',
						prop:'video',
						type:'upload'
					}
				]
				this.showFlag = 1;
			},
			remove(val){
				apiMapper.removeGame({
					gameId:val.game_id
				}).then( data =>{
					this.$message.success('删除成功')
					this.getList();
				})
			},
			closeDialog(){
				this.showFlag = 2;
			},
			addNewGame(){
				this.detailData = [
					{
						label:'游戏类型',
						prop:'type_id',
						type:'select',
					},{
						label:'游戏名称',
						prop:'game_name',
						type:'input'
					},{
						label:'描述',
						prop:'abstract',
						type:'input'
					},{
						label:'详情',
						prop:'detaile',
						type:'input'
					},{
						label:'热度',
						prop:'hot',
						type:'input'
					},{
						label:'下载地址',
						prop:'download_urls',
						type:'input'
					},{
						label:'展示图片',
						prop:'photoes',
						type:'upload'
					},{
						label:'友情链接',
						prop:'urls',
						type:'input'
					},{
						label:'视频信息',
						prop:'video',
						type:'upload'
					}
				];
				this.detailForm = {
					hot:"0",
					photoes:'',
					video:''
				};
				this.showFlag = 1;
			},
			submit(val){
				let params = Object.assign({},val,{
					userId:sessionStorage.getItem('userId')
				});
				params.urls = params.urls.split(',');
				params.download_urls = params.download_urls.split(',');
				Object.assign(params,{
					gameName:params.game_name,
					downloadUrls:params.download_urls,
					typeId:params.type_id
				})
				delete params.game_name;
				delete params.download_urls;
				delete params.type_id;
				if(val.game_id){
					Object.assign(params,{
						gameId:val.game_id
					});
					delete params.game_id
					apiMapper.updateGame(params).then(data=>{
						this.closeDialog();
						this.getList();
					})
				}else{
					apiMapper.saveGame(params).then(data=>{
						this.closeDialog();
						this.getList();
					})	
				}
				
			}
		},
		components:{
			dataList,
			detail,
			myDialog
		}
	}
</script>

<style scoped>
	.add-comp{
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin-bottom: 30px;
	}
</style>


