import axios from 'axios';
import env from '@/axios/env.js';

import qs from 'qs';
function getToken(){
	return {
		"token":localStorage.getItem('token'),
		"Content-Type":"application/x-www-form-urlencoded"
	};
}
function getToken2(){
	return {
		"token":localStorage.getItem('token'),
		"Content-Type":"application/json"
	}
}
function getData(url,params,headers){
	return axios.get(`${env+url}`,{
		params,
		headers
	}).then((res)=>{
		return res; 
	})
}
function postData(url,data,headers){
	return axios.post(`${env+url}`,
		qs.stringify(data),
		{headers}
	).then((res)=>{
		return res; 
	})
}
function postData2(url,data,headers){
	return axios.post(`${env+url}`,
		data,
		{headers}
	).then((res)=>{
		return res; 
	})
}
function getCommentData(url,params,headers){
	return axios.get(`${commentEnv+url}`,{
		params,
		headers
	}).then((res)=>{
		return res; 
	})
}
