<template>
	<div class="userManage-page">
		<data-list 
			:headers="headers" 
			:action="actions" 
			:data="data"
			@detail="detail"
			@remove="remove"
			>
		</data-list>
		<my-dialog :showFlag="showFlag" @closeDialog="closeDialog" name="账号详情">
			<detail :showBtn="false" :data="detailData" :form="detailForm" @submit="submit"></detail>
		</my-dialog>
	</div>
</template>

<script>
	import dataList from '@/components/manage/dataTable'
	import myDialog from '@/components/manage/dialog'
	import detail from '@/components/manage/detail'
	import {apiMapper} from '@/axios/axios'
	export default{
		data(){
			return{
				headers:[
					{
						label:'账号',
						prop:'username'
					},{
						label:'密码',
						prop:'password'
					}
				],
				actions:['detail','remove'],
				data:[],
				showFlag:0,
				detailData:[
					{
						label:'账号',
						prop:'username',
						type:'text'
					},{
						label:'密码',
						prop:'password',
						type:'text'
					},{
						label:'注册时间',
						prop:'create_time',
						type:'text'
					},{
						label:'头像',
						prop:'photo',
						type:'text'
					},{
						label:'真实姓名',
						prop:'real_name',
						type:'text'
					},{
						label:'手机号',
						prop:'phone',
						type:'text'
					},{
						label:'QQ',
						prop:'qq',
						type:'text'
					},{
						label:'Email',
						prop:'email',
						type:'text'
					}
				],
				detailForm:{},
				page:1,
				total:0,
				username:''
			}
		},
		mounted(){
			this.getList();
		},
		methods:{
			getList(){
				apiMapper.getUserList({
					username:this.username,
					page:this.page,
					limit:10
				}).then(data=>{
					this.data = data.data.data.data;
					this.total = data.data.data.total;
				})
			},
			detail(val){
				this.detailForm = val;
				this.showFlag = 1;
			},
			remove(val){
				apiMapper.removeUser({
					userId:val.user_id
				}).then(data=>{
					this.$message.success('删除成功');
					this.getList();
				})
			},
			submit(val){
				console.log(val);
			},
			closeDialog(){
				this.showFlag = 2;
			}
		},
		components:{
			dataList,
			detail,
			myDialog
		}
	}
</script>

<style scoped>
	
</style>


