<template>
	<div class="Home-page">
		<el-container style=" border: 1px solid #eee">
			<el-container>
				<el-aside width="240px" style="border-right: 1px solid gainsboro;">
					<el-menu>
						<el-submenu index="1">
					        <template slot="title">
					          <i class="el-icon-location"></i>
					          <span>用户管理</span>
					        </template>
					        <el-menu-item index="1-1" @click="go('userManage')">
					        <i class="el-icon-menu"></i>
					        <span slot="title">普通用户管理</span>
					      </el-menu-item>
					        <el-menu-item index="1-2" @click="go('managerManage')">
					        <i class="el-icon-menu"></i>
					        <span slot="title">管理员用户管理</span>
					      </el-menu-item>
					      </el-submenu>
					      <el-menu-item index="2" @click="go('gameManage')">
					        <i class="el-icon-setting"></i>
					        <span slot="title">游戏管理</span>
					      </el-menu-item>
					      <el-menu-item index="3" @click="go('typeManage')">
					        <i class="el-icon-setting"></i>
					        <span slot="title">类型管理</span>
					      </el-menu-item>
					      <el-menu-item index="4" @click="go('commentManage')">
					        <i class="el-icon-setting"></i>
					        <span slot="title">评论管理</span>
					      </el-menu-item>
					</el-menu>
				</el-aside>
				<el-main>
					<router-view></router-view>
				</el-main>
			</el-container>
		</el-container>
	</div>
</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
  .Home-page{
  	height: 100vh;
  	background: white;
  }
</style>

<script>
  export default {
    data() {
    	return {
    		
    	}
    },
    methods:{
    	go(val){
    		this.$router.push({
    			path:'/'+val
    		})
    	}
    }
  };
</script>


